@import url(https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar{position:fixed;background-color:#121212;transition:width 200ms ease;z-index:100}.navbar-nav{list-style:none;margin:0;padding:0;display:flex;flex-direction:column;align-items:center;height:100%}.nav-logo{width:100%;font-weight:bold;text-transform:uppercase;margin-bottom:1rem;text-align:center;color:#dbdbdb;background-color:#0b0b0b;letter-spacing:0.3ch}.nav-logo svg{transform:rotate(0deg);transition:transform 600ms}.navbar:hover .nav-logo svg{color:#aaa9a9;transform:rotate(-180deg)}.nav-item{width:100%}.nav-item:last-child{margin-top:auto}.nav-link{display:flex;align-items:center;height:5rem;color:#aaa9a9;text-decoration:none;transition:600ms;outline:none}.nav-link svg{min-width:2rem;margin:0 1.5rem}.nav-link:hover,.navbar:hover .nav-link-active{color:#dbdbdb;background-color:#0b0b0b}.nav-link:hover svg,.nav-link-active svg{color:#e7b917}.nav-link-text{display:none;min-width:150px;margin-left:1rem;font-weight:700;text-transform:uppercase}@media only screen and (max-width: 768px){.navbar{bottom:0;width:100vw;height:5rem}.nav-logo{display:none}.navbar-nav{flex-direction:row}.nav-link{justify-content:center}main{margin:0}}@media only screen and (min-width: 768px){.navbar{top:0;width:5rem;height:100vh}.navbar:hover{width:250px}.navbar:hover .nav-link-text{display:block}}.movie-list-wrapper{display:flex;justify-content:center}ul.movie-list{width:100%;max-width:400px}.movie-list li{display:flex;position:relative;justify-content:space-between;height:180px;margin-bottom:20px;background-color:#0b0b0b;overflow:hidden;border-radius:1px;min-width:320px;max-width:400px}.movie-list li .poster-wrapper{width:130px;margin-right:10px}.movie-list li .poster-wrapper img{max-width:100%}.movie-list li :nth-child(2){display:flex;flex-direction:column;justify-content:space-between;width:calc(100% - 130px);padding:0 5px 5px 0;overflow:hidden}.movie-list li :nth-child(2) h3{color:#fff;width:calc(100% - 30px)}.movie-list li :nth-child(2) h3 small{color:#fff;font-size:0.7em}.movie-list li :nth-child(2) p{position:relative;color:#dbdbdb;width:100%;font-size:12px;margin:0}.movie-list li :nth-child(2) .movie-list-item-more{position:relative;display:flex;justify-content:flex-end;align-items:center;height:30px;width:100%;font-weight:400}.movie-list li :nth-child(2) .movie-list-item-more a{text-transform:capitalize;color:#e7b917;z-index:10}.movie-list li :nth-child(2) .movie-list-item-more .more-gradient{position:absolute;bottom:10px;right:0;height:30px;width:100%;z-index:5;background:linear-gradient(to bottom, rgba(11,11,11,0.32) 0%, rgba(11,11,11,0.61) 42%, #0b0b0b 100%);filter:progid:DXImageTransform.Microsoft.gradient( startColorstr='#520b0b0b', endColorstr='#0b0b0b',GradientType=0 )}.movie-list li :last-child .watch-later-btn{position:absolute;top:0;right:0;display:flex;justify-content:center;align-items:center;border:none;color:#000;font-weight:700;background-color:#303030;width:30px;height:30px;cursor:pointer}.movie-list li :last-child .saved{color:#121212;background-color:#e7b917}@media only screen and (min-width: 375px){.movie-list li{height:185px}}@media only screen and (min-width: 768px){ul.movie-list{max-width:768px}ul.movie-list li{float:left;width:calc((768px / 2) - 20px);margin-right:20px}ul.movie-list li:nth-child(2n){margin-right:0}}@media only screen and (min-width: 1000px){ul.movie-list{max-width:820px}ul.movie-list li{float:left;width:400px}}@media only screen and (min-width: 1200px){ul.movie-list{max-width:1200px}ul.movie-list li{width:380px}ul.movie-list li:nth-child(2n){margin-right:20px}ul.movie-list li:nth-child(3n){margin-right:0}}body::-webkit-scrollbar{width:0.25rem}body::-webkit-scrollbar-track{background:#1e1e24}body::-webkit-scrollbar-thumb{background:#e7b917}:root{font-size:16px}*,*:before,*:after{box-sizing:border-box}body{font-family:"Roboto", Arial, Helvetica, sans-serif;background-color:#000}body,ul,li{margin:0;padding:0}.content{grid-area:content;display:flex;flex-direction:column;justify-content:space-around;align-items:center;padding:0 !important}.content .heading,.content .empty-watchlist-notification{color:#dbdbdb}.footer{grid-area:footer}.wrapper{color:#444}.wrapper{width:100%;height:100%;display:grid;grid-gap:1em;grid-template-areas:"header" "content" "footer"}.box{padding:10px}.footer{background-color:transparent}@media only screen and (min-width: 1000px){.content{width:calc(100% - 5rem);margin-left:5rem}}

