// Fonts
@import url("https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap");

// Variables
@import "variables";
// @import "./header.scss";
@import "./MainNav.scss";
@import "./movie-list.scss";

@import "./scrollbar.scss";

:root {
  font-size: 16px;
}

html {
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

body {
  font-family: "Roboto", Arial, Helvetica, sans-serif;
  background-color: $body-bg;
}

body,
ul,
li {
  margin: 0;
  padding: 0;
}

.content {
  grid-area: content;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  padding: 0 !important;
  .heading,
  .empty-watchlist-notification {
    color: $grey-lightest;
  }
}

.footer {
  grid-area: footer;
}

.wrapper {
  color: #444;
}

.wrapper {
  width: 100%;
  height: 100%;
  display: grid;
  grid-gap: 1em;
  grid-template-areas:
    "header"
    "content"
    "footer";
}

